header{
    background-color: #607274;
    padding: 5px;
    text-align: center;
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 125px;
    top: 0;
    left: 0;
    
}

header h1{
    color: white;
    border: 8px solid white;
    padding: 6px 12px;
    display: inline-block;
    border-radius: 10px;

}